const uni = require('uni').default;

















































import { mapState, mapGetters, mapMutations } from 'vuex'
import txtmessage from '../../components/message.vue'


// 导入公共方法
const PUBLIC = require('../../static/js/public.js')

export default {
	data(){
		return {
			listdata: []
		}
	},
	computed: {
		...mapGetters([
			'GET_CODE',
		]),
		...mapState([
			'HTTPS_URL',
			'WSS_URL'
		])
	},
	components: {
		'txtmessage': txtmessage
	},
	onLoad(query){
		this.getList()
	},
	// 下拉刷新
	onPullDownRefresh(){
		let _this = this
		// 下拉刷新
		_this.getList().then(()=>{
			setTimeout(()=>{
				wx.stopPullDownRefresh()
			}, 1000)
		})
	},
	// 上拉加载
	onReachBottom(){
		console.log('滚动加载')
	},
	methods: {
		...mapMutations([
			'FILE_TIME'
		]),
		zhendong(){
			wx.vibrateShort()
		},
		goto(id, status){
			// 判断是否失效的名片
			if(status == 1){
				uni.navigateTo({
					url: `/pages/page/page?id=${id}`, 
				})
			}else{
				return
			}
		},
		// 获取名片列表 GET
		getList(){
			let _this = this
			return PUBLIC.GET({
				url: `${_this.HTTPS_URL}/cardPocket/list`
			}).then((data)=> {
				let newlist = data.cards
				for(let item of newlist){
					item.crTime = PUBLIC.RETURN_NEWTIME_X(item.crTime)
					item.createTime = PUBLIC.RETURN_NEWTIME_X(item.createTime)
				}
				_this.listdata = data.cards
			})
		}
	}
}



